body {
    font-family: 'Arial', sans-serif;
    background-color: #f5f5f5;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.app {
    text-align: center;
}

h1 {
    color: #333;
    font-weight: 700;
}

.title {
    margin-bottom: 20px;
}

    .title h1 {
        color: #333;
        font-weight: 700;
        margin-bottom: 10px;
    }

    .title p {
        color: #555;
        text-align:left;
    }

.board {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}

.board-map {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: 2px solid #333;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
}

.row {
    display: flex;
}

.cell {
    width: 50px; /* ï¿½}ï¿½Xï¿½Ì•ï¿½ï¿½ğ’²ï¿½ */
    height: 50px; /* ï¿½}ï¿½Xï¿½Ìï¿½ï¿½ï¿½ï¿½ğ’²ï¿½ */
}

.cursor {
    border: 1px solid #000;
}

.flash {
    animation: flash 0.2s linear infinite;
}

.controls {
    display: flex;
    justify-content: center;
    align-items: center; /* ï¿½ã‰ºï¿½Ìƒ{ï¿½^ï¿½ï¿½ï¿½ğ’†‰ï¿½ï¿½É”zï¿½u */
    margin-top: 0px;
}

.up-down-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.left-right-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.button-ok {
    margin: 5px 5px; /* ï¿½{ï¿½^ï¿½ï¿½ï¿½Ìƒ}ï¿½[ï¿½Wï¿½ï¿½ï¿½ğ’²ï¿½ */
    font-size: 25px;
    color: black;
    width: 80px;
    height: 80px;
}

button {
    margin: 5px; /* ï¿½{ï¿½^ï¿½ï¿½ï¿½Ìƒ}ï¿½[ï¿½Wï¿½ï¿½ï¿½ğ’²ï¿½ */
    padding: 10px 10px; /* ï¿½{ï¿½^ï¿½ï¿½ï¿½Ìƒpï¿½fï¿½Bï¿½ï¿½ï¿½Oï¿½ğ’²ï¿½ */
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

    button:focus {
        outline: none;
    }

.arrow {
    width: 0;
    height: 0;
    position: relative;
    border-top: 12px solid transparent; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
    border-bottom: 12px solid transparent; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
    border-left: 12px solid transparent; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
    border-right: 12px solid transparent; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
}

.arrow-up {
    top: -6px;
    border-bottom: 12px solid black; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
}

.arrow-down {
    bottom: -6px;
    border-top: 12px solid black; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
}

.arrow-left {
    left: -6px;
    border-right: 12px solid black; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
}

.arrow-right {
    right: -6px;
    border-left: 12px solid black; /* ï¿½ï¿½ï¿½ÌƒTï¿½Cï¿½Yï¿½ğ’²ï¿½ */
}

.red-cursor {
    border: 1px solid #000;
    background-color: red;
}

.blue-cursor {
    border: 1px solid #000;
    background-color: blue;
}

.black-cursor {
    border: 1px solid #000;
    background-color: black;
}

.black-blue-cursor {
    border: 1px solid #000;
    background: linear-gradient(-45deg, black 0%, black 50%, blue 50%, blue 100%);
}

.red-blue-cursor {
    border: 1px solid #000;
    background: linear-gradient(-45deg, red 0%, red 50%, blue 50%, blue 100%);
}

.stars {
    margin-top: 10px;
    font-size: 30px;
}

.not-clear {
    margin-top: 10px;
    font-size: 25px;
}

.clear {
    margin-top: 10px;
    font-size: 30px;
}

.texts{
    height: 100px;
}

.sns-button {
    margin-top: 20px;
}

    .sns-button a {
        display: inline-block;
        padding: 10px 20px;
        font-size: 16px;
        font-weight: bold;
        text-decoration: none;
        color: #fff;
        background-color: #000000;
        border-radius: 5px;
        transition: background-color 0.3s ease;
    }

@media (max-width: 768px) { /* ï¿½Xï¿½}ï¿½[ï¿½gï¿½tï¿½Hï¿½ï¿½ï¿½Ì‰ï¿½ÊƒTï¿½Cï¿½Yï¿½È‰ï¿½ï¿½Ìê‡ï¿½ÌƒXï¿½^ï¿½Cï¿½ï¿½ */
    .board {
        max-width: 100%;
        padding: 10px;
    }

    .controls {
        margin-top: 10px;
    }

    button {
        padding: 8px 16px;
    }
}
